import React, { useState, useEffect } from 'react';
import { Select, Input } from 'antd';
import axios from 'axios';
import Sidebar from '../../components/Sidebar';
import CustomButton from '../../components/CustomButton';
import CustomInput from '../../components/CustomInput';
import CustomSelect from '../../components/CustomSelect';
import CircleButton from '../../components/CircleButton';
import Calendary from '../../components/Calendary'; 

import './styles.css';

const getCookie = (name) => {
  const value = `; ${document.cookie}`;
  const parts = value.split(`; ${name}=`);
  if (parts.length === 2) return parts.pop().split(';').shift();
  return '';
};

function LabAdd() {
  const [inputMarca, setInputMarca] = useState('');
  const [inputModelo, setInputModelo] = useState('');
  const [inputId, setInputId] = useState('');
  const [inputPatrimonio, setInputPatrimonio] = useState('');
  const [configuracao, setConfiguracao] = useState(''); 
  const [descricao, setDescricao] = useState(''); 
  const [isComputador, setIsComputador] = useState(true); 
  const [data, setData] = useState(''); 
  const [showCalendar, setShowCalendar] = useState(false); 
  const [error, setError] = useState(null); 
  const [selectedStatus, setSelectedStatus] = useState(null);
  
  const handleDateSelect = (selectedDate) => {
    setData(selectedDate);
    setShowCalendar(false);
  };

  const handleChange = (value) => {
    console.log(`selected ${value}`);
  };
  
  const { TextArea } = Input;

  const handleSave = async () => {
    try {
      const csrfToken = getCookie('csrftoken');
      await axios.post(`http://127.0.0.1:8000/api/lab-create/`, {
        id_dispositivo: 2,
        id_sala: 1,
        marca: inputMarca,
        modelo: inputModelo,
        patrimonio: inputPatrimonio,
        is_computador: isComputador,
        configuracao: configuracao,
        descricao: descricao,
        status: "Funcionando",
        data_verificacao: data,
      }, {
        headers: {
          'X-CSRFToken': csrfToken, 
        }
      });
    } catch (error) {
      console.error("Erro ao salvar dados:", error);
      setError('Erro ao salvar dados');
    }
  };

  return (
    <main className='LaboratoriesLabAdd'>
      <section className='principalLabAdd'>
        <Sidebar />
        <div className='mainContentLabAdd'>
          <div className='headerLabAdd'>
            <h1 className='headerTitleLabAdd'>Adicionar Dispositivos</h1>
          </div>
          <div className='addDiv'>
            {error && <p>{error}</p>}

            <div style={{ marginTop:"15px" ,marginBottom: "15px" ,display : 'flex' , gap: '30px' , alignItems: "center" , justifyContent: 'space-between'}}>

            
              <div className='tipoContainer'>
                <p className='tipop'>Tipo:</p>
                <div className='radioContainer'>
                <label>
                  <input
                    type="radio"
                    name="tipo"
                    checked={isComputador}
                    onChange={() => setIsComputador(true)}
                  />
                  Computador
                </label>
                <label style={{marginleft: '5px'}}>
                  <input
                    type="radio"
                    name="tipo"
                    checked={!isComputador}
                    onChange={() => setIsComputador(false)}
                  />
                  Outro
                </label>
                </div>
                
              </div>

              <CustomInput
                label="Dispositivo"
                placeholder="Tipo"
                value={inputMarca}
                onChange={(e) => setInputMarca(e.target.value)}
                className="input-field22050"
              />

              <CustomInput
                label="Patrimonio"
                placeholder="Patrimnio"
                value={inputPatrimonio}
                onChange={(e) => setInputPatrimonio(e.target.value)}
                className="input-field320"
              />

            </div>

            <div style={{ marginTop:"15px" ,marginBottom: "15px" ,display : 'flex' , gap: '35px' , alignItems: "center" , justifyContent: 'space-between'}}>
              <div className="select-container">
                <label htmlFor="statusSelect" className="select-label">
                  Status
                </label>
                <Select
                    className='customSelect'
                    placeholder="Status"
                    style={{
                      color: '#4F4F4F',
                      border: '1px solid #BDBDBD', 
                      borderradius: '4px ',
                      width: 250,
                      height: 50,
                      fontSize: 'clamp(10px, 2vw, 14px)',

                    }}
                    onChange={handleChange}
                    options={[
                      {
                        value: 'Funcionando',
                        label: 'Funcionando',
                      },
                      {
                        value: 'Com Problema',
                        label: 'Com Problema',  
                      },
                    ]}
                />
              </div>
              <CustomInput
                label="ID"
                placeholder="ID"
                value={inputId}
                onChange={(e) => setInputId(e.target.value)}
                className="input-field22050"
              />

              <CustomInput
                label="Marca/Modelo"
                placeholder="Modelo"
                value={inputModelo}
                onChange={(e) => setInputModelo(e.target.value)}
                className="input-field320"
              />
            </div>
            
            <div style={{ marginTop:"15px" ,marginBottom: "15px" ,display : 'flex' , gap: '30px' , alignItems: "center" , justifyContent: 'space-between'}}>
              <div className="select-container">
              <label htmlFor="statusSelect" className="select-label">
                  Configuraes
                </label>
                <TextArea 
                  rows={7} 
                  placeholder="Configuraes" 
                  style={{
                    width: 416,
                    height: 172,
                    resize: 'none',
                  }}
                /> 
              </div>        
              
              <div className="select-container">
                <label htmlFor="statusSelect" className="select-label">
                  Descrio
                </label>    
                <TextArea 
                  rows={7} 
                  placeholder="Descrio" 
                  style={{
                    width: 416,
                    height: 172,
                    border: '1px solid #BDBDBD',
                    borderRadius: '4px',
                    resize: 'none',
                  }}
                />  
              </div>    

            </div>

            
            <div className='dateInputContainer'>
              <CustomInput
                placeholder="Data de Verificao"
                value={data}
                readOnly
                onClick={() => setShowCalendar(!showCalendar)}
                className="input-field180"
              />
              <CircleButton iconType="calendar" onClick={() => setShowCalendar(!showCalendar)} />
              {showCalendar && <Calendary onDateSelect={handleDateSelect} />}
            </div>
    
            <CustomButton onClick={handleSave} label="Salvar" className="blue size138"></CustomButton>
          </div>
        </div>
      </section>
    </main>
  );
}

export default LabAdd;
